.search-div{
	width:200px;
	height:30px;
	position: relative;
	float: right;
	top:-2px;
	margin-right: 10px;
	z-index: 100;
}
.search-input-div{
	width:100%;
	height:100%;
	z-index: 101;
	position: absolute;
	top: 0;
	left: 0;
}
.search-input-div input{
	width: 100%;
	height: 100%;
	padding: 0px 10px;
	position: relative;
	border-radius: 15px;
	z-index: 103;
	padding-right: 32px;
}
.search-icon-div{
	width:30px;
	height:100%;
	position: absolute;
	z-index: 102;
	padding:3.4px;
	padding-left:7px;
	top:0px;
	right: 0;
	color:#3498DB;
	background-color: ;
	border-radius: 15px;
	cursor:pointer;
}

.paths ul{
	border:0px solid red;
	width: 605px;
	margin:0;
	padding:0;
	position: relative;
}
.paths ul li{
	list-style: none;
	display: inline-block;
	position: relative;
}
.paths ul li a div{
	padding:5px 10px;
	border: 0px solid red;
	position: relative;
}
.paths ul li ul{
	position: absolute;
	display: none;
	background-color: #21618C;
	width: 200px;
	border-radius: 5px;
	box-shadow:3px 3px 3px rgba(0,0,0,0.1),-3px 0px 3px rgba(0,0,0,0.1);
	padding-top: 10px;
	margin-top: 0px;
	border: 0px solid red;
}
.paths ul li ul li a{
	width: 100%;
}
.paths ul li ul li a div{
	width: 100%;
	border: 0px solid red;
	padding: 7px 10px;
}

.paths ul li:hover > ul{
	/*display: block;*/
}
.paths ul li ul li{
	margin: 0px;
	padding: 0px;
	width: 100%;
	position: relative;
	border:0px solid transparent;
}
.paths ul li .checkboxsub{
	position: fixed;
	top:0;
	left: 0;
}
.paths ul li .subuldisplayOff{
	display: none;
}
.paths ul li .checkboxsub:checked ~  .subuldisplayOff{
	display: block;
}
.paths ul li .links{
	text-decoration: none;
	color: white;
	display: inline-block;
	/*border:1px solid gray;*/
	/*background-color: lightgray;*/
	/*width: 150px; not working*/
	border-radius: 4px;
	box-sizing: border-box;
	position: relative;
	z-index: 25;
}
.paths ul li .links:hover{
	background-color: ;
	text-decoration:none; /* this is required if u use bootstrap4 cdn */
	color:white;
}

.paths ul li .links span{
	position:absolute;
	height: 1px;
	bottom:-1px;
	left:50%;
	width:0%;
	background-color: white;
	color:white;
	overflow: hidden;
	visibility: ;
	transition: all 0.1s linear;
}
.paths ul li  .links:hover > div span{
	left:3%;
	width: 96%;
	transition: all 0.3s linear;
}

.paths ul li ul li .links:hover > div span{
	left:0%;
	width: 100%;
	transition: all 0.3s linear;
}
.links:after{
	content: "";
	position: absolute;
	top: 0;
	left: 0%;
	width: 0%;
	height: 100%;
	background-color: rgba(0,0,0,0.1);
	z-index: -1;
	border-radius: 4px;
	transition: all 0.1s linear;
}
.links:hover::after{
	width: 100%;
	transition: all 0.3s linear;
}


.subuldisplayOff{
	display: none;
}
.subuldisplayOn{
	display: block;
}
@media screen and (max-width: 840px){
	.toggleNavbtn{
		display: block;
		top:-25px;
	}

	.search-div{
		top:-25px;
	}

	.navulmain{
		display: none;
	}
	.togglecheckbox:checked ~ .navulmain{
		display: block;
	}

	.paths ul li{
		display: block;

	}
	.paths ul li a div{
		position: relative;
		padding: 7px 10px;
	}
	.paths ul{
		margin-top: 35px;
		width: 200px;
		min-height: 300px;
		border-radius: 5px;
		box-shadow:3px 3px 3px rgba(0,0,0,0.1),-3px 0px 3px rgba(0,0,0,0.1);
	}
	.paths ul li a{
		width: 100%;
		
	}
	.paths ul li ul{
		left: 199px;
		top: 0px;
		padding-top: 0px;
		margin-top: 0px;
		min-height: 157px

	}
	.paths ul li ul li a div{
		padding: 7px 10px;
	}
	
}

@media screen and (max-width: 420px){
	.paths ul{
		width: 100%;
		position: relative;
	}
	ul li{
		position: relative;
	}
	ul li ul{
		position: unset;
	}
